import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import Footer from "../../components/Footer";
import Hidden from "@material-ui/core/Hidden";
import Particles from "react-particles-js";
import Container from "@material-ui/core/Container";
import SimpleBreadcrumbs from "../../layout/SimpleBreadcrumbs";
import Grid from "@material-ui/core/Grid";
import { getHeaderBackgroundColor } from "../../utils/Theme";
import { useTheme } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";
import MediaCard from "../../components/MediaCard";
import Skeleton from "@material-ui/lab/Skeleton";
import Box from "@material-ui/core/Box";
import defaultImage from "../../assets/img/defaults/image_not_found_600x450.jpg";

const importAll = (r) => r.keys().map(r);

const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexDirection: "column",
    [theme.breakpoints.between("xs", "md")]: {
      minHeight: "calc(100vh - 131px)",
    },
    [theme.breakpoints.up("md")]: {
      minHeight: "calc(100vh - 225px)",
    },
  },
}));
export default function List2() {
  const classes = useStyles();
  const theme = useTheme();
  const { slug } = useParams();
  const [posts, setPosts] = useState([]);
  const data = new Map();
  const images = new Map();

  const files = importAll(
    require.context("../../assets/md", true, /[.md .jpg .png]$/)
  );

  const isJpgOrPng = (file) => {
    return (
      file.default &&
      (file.default.includes(".jpg") || file.default.includes(".png"))
    );
  };

  const isMdFile = (file) => {
    return file.default && file.default.includes(".md");
  };

  const getFileName = (file) => {
    return file.default.substring(file.default.lastIndexOf("/") + 1);
  };

  const getPath = (file) => {
    return file.default.substring(0, file.default.lastIndexOf("/"));
  };

  const isPostHeaderImage = (file) => {
    return data.has(getPath(file)) && getFileName(file).includes("post-header");
  };
  const isThumbImage = (file) => {
    return (
      data.has(getPath(file)) && getFileName(file).includes("thumb400x300")
    );
  };
  const getThumbName = (file) => {
    //console.log(file.default);
    return file.default.substring(
      file.default.lastIndexOf("/") + 1,
      file.default.lastIndexOf(".")
    );
  };
  const isCategoryThumbImage = (file) => {
    //console.log("CategoryThumbImage");
    //console.log(file);
    return getFileName(file).includes("thumb400x300");
  };
  const getThumbExtension = (file) => {
    return file.default.substring(file.default.lastIndexOf(".") + 1);
  };
  const updateItem = (path, fileName) => {
    const obj = data.get(path);
    obj.path = path;
    obj.fileName = fileName;
    obj.type = "postFolder";
  };

  const createItem = (path, fileName) => {
    const paths = path.split("/");
    const filteredPaths = paths.filter((e) => e);

    data.set(path, {
      path: path,
      postName: fileName,
      categoryName: filteredPaths[2],
      fileName: fileName,
      type: "postFolder",
    });
  };

  const postHeaderImageName = (file) => {
    return getFileName(file).substring(0, getFileName(file).lastIndexOf("."));
  };
  const postHeaderImageExtension = (file) => {
    const fileName = file.default.substring(file.default.lastIndexOf("/") + 1);

    return fileName.substring(fileName.lastIndexOf(".") + 1);
  };

  const getCards = () => {
    //console.log("IMAGES2");
    //console.log(images);
    let postPage = [];

    if (posts !== null) {
      posts.forEach((post, i) => {
        //console.log("====post====");
        //console.log(post);
        //console.log("====slug===");
        //console.log(slug);
        if (
          post &&
          post.hasOwnProperty("type") &&
          post.type === "postFolder" &&
          post.categoryName === slug.toLowerCase()
        ) {
          const item = (
            <Grid item xs={12} sm={6} md={4} key={i}>
              <MediaCard post={post} defaultImage={defaultImage} />
            </Grid>
          );
          postPage.push(item);
        }
      });
      return postPage;
    } else {
      return getSkeletons();
    }
  };

  const getSkeletons = () => {
    let items = [];
    for (let i = 0; i < 9; i++) {
      items[i] = (
        <Grid key={i} item xs={12} sm={4} style={{ maxWidth: 345 }}>
          <Box style={{ width: "100%" }} marginRight={0.5} my={1}>
            <Skeleton variant="rect" style={{ width: "100%" }} height={170} />
            <Box>
              <Skeleton />
              <Skeleton width="60%" />
            </Box>
          </Box>
        </Grid>
      );
    }
    return items;
  };

  useEffect(() => {
    files.forEach((file) => {
      if (isMdFile(file)) {
        const fileName = getFileName(file);
        const path = getPath(file);

        if (data.has(path)) {
          updateItem(path, fileName);
        } else {
          createItem(path, fileName);
        }
        //mdFiles[path] = { path: path, fileName: fileName, type: "postFolder" };
      }

      if (isJpgOrPng(file)) {
        if (isPostHeaderImage(file)) {
          //console.log("---===isPostHeaderImage===---");
          const obj = data.get(getPath(file));
          obj.postHeaderImageName = postHeaderImageName(file);
          obj.postHeaderImageExtension = postHeaderImageExtension(file);
          //console.log("-----------------------------");
          if (images.has(getPath(file))) {
            const obj = images.get(getPath(file));
            obj.postHeaderImageName = postHeaderImageName(file);
            obj.postHeaderImageExtension = postHeaderImageExtension(file);
          } else {
            images.set(getPath(file), {
              postHeaderImageName: postHeaderImageName(file).substring(
                0,
                postHeaderImageName(file).length - 2
              ),
              postHeaderImageExtension: postHeaderImageExtension(file),
            });
          }
        } else if (isThumbImage(file)) {
          const obj = data.get(getPath(file));
          obj.thumbName = getThumbName(file);
          obj.thumbExtension = getThumbExtension(file);
        } else if (isCategoryThumbImage(file)) {
        }
      }
    });

    setPosts(data);
    //console.log("IMAGES");
    //console.log(images);
    document.title = document.title + " - " + slug;
  }, []);

  return (
    <React.Fragment>
      <Hidden only={["xs", "sm"]}>
        <Particles
          height={150}
          style={{ backgroundColor: getHeaderBackgroundColor(theme) }}
        />
      </Hidden>
      <Container maxWidth="md" className={classes.container}>
        <SimpleBreadcrumbs
          links={[{ title: "Главная", link: "/" }]}
          current={slug}
        />
        <br />
        <main>
          <Grid
            container
            spacing={2}
            style={{ marginBottom: theme.spacing(3) }}
          >
            {getCards()}
          </Grid>
        </main>
      </Container>
      <Footer />
    </React.Fragment>
  );
}
