import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import { Link } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
    borderRadius: 0,
  },
  link: {
    color: theme.palette.text.primary,
    textDecoration: "none",
    "&:hover": {
      textDecoration: "none",
    },
  },
}));

export default function ImgMediaCard({ post }) {
  const classes = useStyles();
  const [title, setTitle] = useState();
  const [content, setContent] = useState();
  const [thumb, setThumb] = useState(
    "/static/img/defaults/image_not_found_600x450.jpg"
  );
  const { path, categoryName, postName } = post;

  useState(() => {
    fetch(path + "/" + post.fileName)
      .then((res) => {
        if (res.ok) {
          //console.log("OK");
          return res.text();
        } else {
          //console.log("-NO-");
          return "error.png";
        }
      })
      .then((text) => {
        //console.log("TEXT");
        setContent(text);
        let t = text.replace(/[`~!@#$%^&*()_|\-=?;'",<>\{\}\[\]\\\/]/gi, ""); //+
        if (t.length > 0) {
          setTitle(t.split("\n")[0]);
        }
      });

    let imagePath = "/static/img/defaults/empty.jpg";
    if (post.thumbName && post.thumbExtension) {
      imagePath = `${post.path}/${post.thumbName}.${post.thumbExtension}`;
      setThumb(imagePath);
    } else {
      const categoryImage = `${post.path.substring(
        0,
        post.path.lastIndexOf("/")
      )}/thumb400x300.jpg`;
      fetch(categoryImage)
        .then((resp) => resp.blob())
        .then((data) => {
          console.log("w: " + data.type);
          if (data && data.type && data.type.includes("image/jpeg")) {
            console.log("super");
            imagePath = categoryImage;
            setThumb(imagePath);
          }
        });
    }
  }, []);
  useState(() => {
    // Remove dot
    if (title != null) {
      if (title.lastIndexOf(".")) {
        setTitle(title.substring(0, title.lastIndexOf(".")).trim());
      } else {
        setTitle(title.trim());
      }
    }
  }, [title]);

  // const getThumbImage2 = () => {
  //   let path = "/static/img/defaults/empty.jpg";
  //   if (post.thumbName && post.thumbExtension) {
  //     path = `${post.path}/${post.thumbName}.${post.thumbExtension}`;
  //     setThumb(path);
  //   } else {
  //     const categoryImage = `${post.path.substring(
  //       0,
  //       post.path.lastIndexOf("/")
  //     )}/thumb400x300.jpg`;
  //     fetch(categoryImage)
  //       .then((resp) => resp.blob())
  //       .then((data) => {
  //         console.log("w: " + data.type);
  //         if (data.type === "image/jpeg") {
  //           console.log("super");
  //           path = categoryImage;
  //           setThumb(categoryImage);
  //         }
  //       });
  //   }
  //   return path;
  // };
  // const getThumbImage = () => {
  //   if (post.thumbName && post.thumbExtension) {
  //     return `${post.path}/${post.thumbName}.${post.thumbExtension}`;
  //   } else {
  //     return `${post.path.substring(
  //       0,
  //       post.path.lastIndexOf("/")
  //     )}/thumb400x300.jpg`;
  //   }
  // };

  return (
    <Card className={classes.root}>
      <Link
        className={classes.link}
        to={{
          pathname: `/category/${categoryName}/post/${post.path.substring(
            post.path.lastIndexOf("/") + 1
          )}/file/${postName.substring(0, postName.lastIndexOf("."))}/name/${
            title != null ? title.trim() : ""
          }`,
          search: "",
          hash: "",
          state: { fromDashboard: true },
          title: title,
          content: content,
          postData: post,
        }}
      >
        {/* <Link
        href={`/category/${categoryName}/post/${title}`}
        target="_blank"
        className={classes.link}
      > */}
        <CardActionArea>
          <CardMedia
            component="img"
            alt="Contemplative Reptile"
            height="140"
            image={thumb}
            title={title}
          />
          <CardContent>
            <Typography gutterBottom variant="button" component="h1">
              {title}
            </Typography>
          </CardContent>
        </CardActionArea>
      </Link>
    </Card>
  );
}
